.updateprogress {

    &  .premium {

        &  h2 {
            margin: 2rem 0;
        }
        
        &  .cta {
            padding: 1rem 2rem;
            margin: 3rem 0;

            &  button {
                padding: 1rem ;
                background-color: transparent;
                border: 1px solid currentColor;
                font-size: 1.2rem;
            }
        }
        &  form {
            margin-bottom: 1rem;
            &  input {
                padding: .7rem;
            }
        }
    }

    &  .stages {
        margin: 8rem 0;
        display: flex;
        justify-content: flex-start;
        align-items: center;

        &  .update {
            font-size: 1.3rem;

            @media only screen and (max-width: $bplarge) {
                width: 80vw;
            }

            &  .pointer {
                background-color: $primary-orange;
                width: 1rem;
                height: 4rem;
                margin-left: 5rem;
                position: relative;

                &:hover ul {
                    height: 100%;
                    visibility: visible;
                    opacity: 1;
                }

                &  ul {
                    visibility: hidden;
                    height: 0;
                    opacity: 0;
                    position: absolute;
                    top: 50%;
                    left: -500%;
                    list-style: none;
                    transition: all .5s;
    
                    &  li {
                        margin-bottom: 1rem;
                        box-shadow: 1px 1px 5px rgb(184, 184, 184);
                        border-left: 1px solid $primary-orange;
    
                        &  button {
                            padding: 1rem 5rem;
                            font-size: 1.2rem;
                            background-color: $light-primary-blue;
                            border: none;
                            transition: all .5s;
                            &:hover {
                                background-color: $primary-blue;
                            }
                        }
                    }
                }
            }

            
        }

        &  label{
            margin-left: 5rem;
        }
    }
    &  .cta {
        display: flex;
        align-items: center;
        margin-left: 5rem;

        &  .primary_btn_orange {
            margin-left: 2rem;
        }
    
        &  .loader {
            width: 3rem;
            margin-left: 1rem;
        }
    
        &  p {
            padding: .5rem 0;
            font-size: 1.3rem;
        }
    }

    
}
.popup {
    position: fixed;
    top: 30vh;
    left: 30vw;
    transform: translate(-50%,-50%);
    width: 50vw;
    background-color: white;
    border-radius: 1rem;
    background-color: $secondary-blue;
    color: $white;
    z-index: 1000;
    box-shadow: 2px 2px 5px black;
        font-size: 1.5rem;

    &  .heading {
        padding: 2rem;
        font-size: 2rem;
    }
    
    &  p {
        padding: 2rem;
    }

    &  input {
        display: block;
        width: 90%;
        padding: 1.5rem;
        font-size: 1.4rem;
        border-radius: .5rem;
        border: none;
        margin: 2rem;
    }

    &  .cta {
        display: flex;
        justify-content: flex-end;
        align-items: center;
        font-size: 1.5rem;
        margin: 2rem;

        &  button {
            padding: 1.5rem 2rem;
            border: none;
            border-radius: .5rem;
            transition: all .5s;
            color: $white;
            box-shadow: 2px 2px 5px rgb(206, 206, 206);
            background-color: $secondary-blue;
            text-align: right;
            margin-right: 2rem;

            &:hover {
                background-color: $white;
                color: $black;
                box-shadow: 2px 2px 5px rgb(206, 206, 206);
            }
        }
        &  .cancel {
            background-color: transparent;
            color: $primary-red;
            border: 2px solid currentColor;
            border-radius: .5rem;
            box-shadow: none;

            &:hover {
                background-color: $primary-red;
                color: $black;
                box-shadow: none;
            }
        }
    }
    
}