.overview {
    display: grid;
    grid-template-columns: repeat(10, 1fr);

    @media only screen and (max-width: $bpsmall) {
        display: block;
    }
    
    &  .details {
        grid-column: 1/7;
        border-radius: 1rem;
        padding: 2rem;
        height: min-content;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        box-shadow: 1px 1px 1rem rgb(223, 223, 223);

        @media only screen and (max-width: $bpsmallest) {
            padding: 1rem;
        }

        &  p {
            font-size: 1.5rem;
            margin: 1rem;

            &  span {
                font-size: 1.3rem;
                font-weight: 100;
                font-style: italic;
                margin-right: 1rem;
            }
        }
        &  .invplan {
            grid-column: 1/-1;
            font-size: 2.5rem;
            margin-top: 3rem;
            color: $primary-orange;
            text-align: center;
            position: relative;

            &::before{
                content: "";
                width: 35%;
                height: .2rem;
                background-color: $primary-orange;
                position: absolute;
                top: 50%;
                left: 0;

            }
            &::after{
                content: "";
                width: 35%;
                height: .2rem;
                background-color: $primary-orange;
                position: absolute;
                top: 50%;
                right: 0;

            }
        }

    }

    &  .investment_details {
        grid-column: 8/-1;
        grid-row: 1/3;
        position: relative;
        z-index: 100;

        @media only screen and (max-width: $bplarge) {
            grid-column: 7/-1;
            margin-left: 2rem;
        }

        @media only screen and (max-width: $bpsmall) {
            padding: 4rem 0;
            margin: 0;
        }

        &  .loader {
            width: 3rem;
        }
        &  button {
            padding: .5rem 1rem;
            text-align: center;
            display: inline;
            margin-top: 1rem;
            margin-left: 1rem;
            font-size: 1.2rem;
            background-color: $secondary-blue;
            border: none;
            color: $white;
        }
        &  .holiday_details {
            margin-top: 2rem;
            padding: 1rem;
            position: relative;
            z-index: -1;
            animation: holidays 1s;
        }
        
        & .holiday {
            padding: 1rem 0;
            border-bottom: 1px solid currentColor;
            
            &  p {
                font-size: 1.3rem;
            }
        }
        
    }

    &  .duedate_info {
        grid-column: 1/7;
        grid-row: 2/3;
        justify-self: center;
        font-style: italic;
        font-weight: 500;
        font-size: 5rem;
        color: $primary-blue;

        @media only screen and (max-width: $bpsmall) {
            text-align: center;
        }
    }

    &  .progressbar {
        grid-column: 1/-1;
        margin-top: 10rem;
        height: 9rem;
        border-left: 1px solid $black;

        &  .bar {
            background-color: $primary-orange;
            height: 75%;
            margin-top: 1rem;
        }

        &  .graduation {
            display: grid;
            grid-template-columns: repeat(9, 1fr);
            justify-content: center;
            border-top: 1px solid black;
            &  span {
                text-align: center;
                border-left: 1px solid black;
            }
        }
    }

    &  .dates {
        grid-column: 1/-1;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-gap: 2rem;
        padding: 2rem 0;

        @media only screen and (max-width: $bpmedium) {
            grid-template-columns: repeat(2, 1fr);
        }

        @media only screen and (max-width: $bpsmallest) {
            grid-template-columns: repeat(1, 1fr);
        }

        &  .date {
            font-size: 2rem;
            font-weight: 900;
            border: 1px solid $primary-blue;

            &  .cont {
                display: flex;
                align-items: center;
            }

            &  .span {
                font-size: 1.5rem;
                background-color: $primary-blue;
                width: min-content;
                padding: 2rem .5rem;
                margin-right: 1rem;
                height: 100%;
            }
            &  p {
                text-align: center;
                font-size: 2rem;
            }
        }

        &  .cleared {
            background-color: rgba(3, 15, 53, 0.205);
        }
    }
}