.pic {
    position: relative;
    overflow: hidden;
    border-radius: 50%;

    &  label {
        opacity: 0;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translate(-50%, 0);
        background-color: rgba(0, 0, 0, 0.699);
        width: 90%;
        text-align: center;
        height: 10%;
        padding: 1rem;
        cursor: pointer;
        transition: all .5s;

        &  .font {
            color: $white;
        }

        &  input {
            display: none;
        }
    }

    &:hover label {
        opacity: 1;
    }

    &  .loader {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%) scale(.2);
    }
}