.stageprogress {

    &  .top {
        display: flex;
        justify-content: space-between;
        align-items: flex-end;
        padding: 2rem;
        padding-top: 0;
        border-bottom: .5rem solid $primary-orange;
        border-radius: 1rem;
        background-color: rgba(236, 236, 236, 0.103);
        @media only screen and (max-width: $bplarge) {
            display: block;
        }

        &  .dates {
            padding-top: 2rem;

            @media only screen and (max-width: $bplarge) {
                padding: 2rem 0;
            }

            &  .box {
                padding: 1rem 2rem;
                box-shadow: 1px 1px .5rem rgb(143, 143, 143);
                margin-top: 1.5rem;
                font-size: 1.5rem;
                display: flex;
                align-items: center;


                &  span {
                    font-size: 1.2rem;
                    margin-left: 2rem;
                }
            }
        }

        &  .stage {
            font-size: 2rem;
            font-weight: 200;
            max-width: 20rem;
            font-style: italic;
        }
    }

    &  .stages {
        margin: 3rem 0;

        &  .box {
            padding: 1rem 2rem;
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: center;
            box-shadow: 1px 1px 5px rgb(184, 184, 184);
            margin: 1rem 0;
            font-size: 1.3rem;
            border-left: 1px solid $primary-orange;

            @media only screen and (max-width: $bplarge) {
                width: 80vw;
            }
        }
    }
}