.create_admin, .register, .editprofile {
    height: 80vh;
    overflow: scroll;
    padding: 2rem;

    &  .form-type {
        display: flex;
        justify-content: flex-start;
        width: 100%;

        &  button {
            width: 50%;
            padding: 2rem;
            font-size: 1.5rem;
            background-color: transparent;
            border: none;
            
            &:hover {
                background-color: rgba(185, 185, 185, 0.247);
            }
        }
        &  .bclicked {
            background-color: rgba(206, 206, 206, 0.247);
            color: $primary-orange;
            border-bottom: .5rem solid currentColor;
        }
    }
    
    &  .premium_form {

        &  .due_date {
            display: flex;
            align-items: center;
            &  .editbtn {
                padding: 1rem;
                border: 1px solid currentColor;
                margin-left: 2rem;
                font-size: 1.3rem;
                cursor: pointer;
                &:hover {
                    background-color: $black;
                    color: $white;
                }
            }
        }
        &  .pnr {
            display: flex;
            align-items: center;
            justify-content: space-between;

            & h2 {
                font-size: 3rem;
                font-weight: 900;
                margin-left: 5rem;
                color: $primary-orange;
            }
        }
    }

    &  .form {
        display: flex;
        flex-direction: column;
        align-items: flex-start;


        &  input {
            padding: 1.5rem;
            font-size: 1.7rem;
            margin: 1rem 0;
            width: 50rem;
            outline-color: $primary-blue;

            @media only screen and (max-width: $bplarge) {
                width: 90%;
            }
        }
        &  label {
            font-size: 1.5rem;

            &  select {
                width: 20rem;
                height: 2rem;
                padding: 1rem;
                margin: 1rem;
            }
        }

        & p {
            font-size: 1.4rem;
            padding: 1rem;
            max-width: 40rem;
            color: $primary-blue;
        }

        .cta {
            display: flex;
            align-items: center;
            justify-content: flex-start;

            &  .loader {
                animation: loader 1.5s infinite alternate;
                width: 3rem;
                margin-left: 2rem;
            }
        }

        &  .invs {
            height: 10rem;
            overflow: scroll;
            width: 100%;

            &  .inv {
                background-color: black;
                padding: .5rem;
                margin: .1rem 0;
                &  p {
                    padding: 0;
                    font-size: 1rem;
                }
                &  .username {
                    font-weight: 900;
                    font-size: 1.2rem;
                }
            }
        }
    }
}

  .officer-container{
    position: relative;
    &:hover {
        &  .listInp {
            display: block;
        }
    }

    &  .listInp {
        display: none;
        width: 80%;
        max-height: 150%;
        overflow: scroll;
        background-color: black;
        color: $white;
        box-shadow: 2px 2px 2rem black;
        position: absolute;
        top: 6rem;
    }
    &  .inpList {
        left: 0;
        padding: .5rem 1rem;
        cursor: pointer;

        &:hover {
            background-color: rgba(66, 66, 66, 0.651);
        }

        &  p {
            font-size: 1.2rem;
            font-weight: 100;
            padding: .5rem 0;
            color: white;
        }
        &  span {
            font-size: 1.3rem;
            font-weight: 900;
            padding: .5rem 0;
        }
    }
}