.recordbook {
    background-color: black;
    height: 100vh;

    &  .particles {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 100%;
        z-index: 1;
    }

    &  .loader {
        width: 3rem;
        margin: 2rem auto;
        animation: loader infinite 1.5s alternate;
        text-align: center;
        margin-left: 10rem;
    }
    &  .loader2 {
        width: 3rem;
        margin-left: 2rem;
        animation: loader infinite 1.5s alternate;
    }

    .book {
        display: flex;
        background-color: rgb(238, 238, 238);
        margin: auto;
        height: 100vh;
        box-shadow: .5rem .5rem 2rem rgb(100, 100, 100);
        position: relative;
        z-index: 100;

        &  .left {
            width: 30%;
            height: 100%;
            overflow: hidden;
            border-right: 1px solid $bordercolor;

            &  input {
                width: 80%;
                margin: 2rem 1rem;
                outline: none;
                font-size: 1.5rem;
                border-radius: 10rem;
                padding: .5rem 2rem;
                border: none;
                height: 3rem;
            }

            &  ul {
                list-style: none;
                height: 87%;
                overflow: scroll;

                &  li {
                    display: flex;
                    align-items: center;
                    padding: 1rem 2rem;
                    font-size: 1.5rem;
                    border-top: 1px solid rgb(211, 211, 211);
                    cursor: pointer;
                    transition: all .5s;

                    &:hover {
                        background-color: $bordercolor;

                        &  .name {
                            font-weight: 900;
                            font-size: 1.7rem;
    
                        }
                    }

                    .img {
                        width: 5rem;
                        height: 5rem;
                        background-color: black;
                        margin-right: 2rem;
                        border-radius: 50%;
                        box-shadow: 1px 1px 5px black;
                    }

                    &  .name {
                        font-weight: 900;
                        font-size: 1.7rem;

                    }
                }
            }
        }
        &  .welcome {
            opacity: .2;
            text-align: center;
            width: 70%;
            margin: auto;
            &  .logo {
                width: 50%;
                margin: auto;

            }
            &  p {
                font-size: 3rem;
                padding: 2rem;
            }
        }
        &  .right {
            width: 70%;
            

            &  header {
                border-bottom: 1px solid $bordercolor;
                padding: 1.5rem 2rem;
                display: flex;
                align-items: center;

                &  .img {
                    width: 5rem;
                    height: 5rem;
                    background-color: black;
                    border-radius: 50%;
                    box-shadow: 1px 1px 5px black;
                    margin-right: 3rem;
                }

                &  .name {
                    font-weight: 900;
                    font-size: 1.8rem;
                }
                &  .details {
                    display: flex;
                    font-size: 1.3rem;
                    margin-top: .5rem;

                    &  p {
                        margin-right: 1rem;
                    }
                }
                &  .reward {
                    align-self: center;
                    margin-left: 10rem;
                    font-weight: 900;
                    text-align: center;
                    &  span {
                        font-weight: 100;
                        font-size: 1.5rem;
                    }
                    &  p {
                        font-size: 2rem;
                        margin: 1rem 0;
                    }
                }
            }
            &  .main {
                height: 80%;

                &  .cta {
                    width: 10%;
                    margin: 2rem auto;
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    &  button {
                        padding: .5rem 1rem;
                        border: 1px solid $black;
                        border-radius: 5px;
                        transition: all .2s;
                        &:hover {
                            background-color: $black;
                            color: $white;
                        }
                    }
                    &  .clicked {
                        background-color: $black;
                        color: $white;
                    }

                }
                
                &  .table-container {
                    max-height: 90%;
                    width: 99%;
                    margin: auto;
                    overflow: scroll;
                    
                    table {
                        max-height: 100%;
                        width: 99%;
                        margin: auto;
                        overflow: scroll;
                        text-align: center;
    
                        
                        &  tr {
                            font-size: 1.2rem;
    
                            &  th {
                                font-size: 1.5rem;
                                padding: 1rem 0;
                                border: 2px solid $bordercolor;
                                height: 2rem;
                            }
    
                            &  td {
                                padding: .5rem 0;
                                border: 1px solid $bordercolor;
                            }
                        }
                    }
                }
            }
        }
    }

    &  .addnew {
        position: fixed;
        bottom: 3rem;
        right: 3rem;
        z-index: 1000;
        width: 5rem;
        height: 5rem;
        border-radius: 50%;
        box-shadow: .1rem .1rem 1.5rem $black;
        background-color: $black;
        transition: all .5s;

            &:hover {
                transform: scale(1.1);
            }

        &  button {
            font-size: 4rem;
            margin: auto;
            width: 100%;
            height: 100%;
            background-color: transparent;
            border: none;
            color: $white;
            transition: all .5s;
        }
    }
    &  .form {
        width: 25vw;
        position: fixed;
        right: 2rem;
        bottom: 2rem;
        padding-left: 2rem;
        background-color: rgba(0, 0, 0, 0.849);
        box-shadow: 1px 1px 5px $white;
        transform: translateX(0);
        transition: all .5s ease-in;
        z-index: 1000;

        border-radius: 1rem;
        border-bottom-right-radius: 3rem;
        padding-bottom: 8rem;
        color: $white;

        &  h2 {
            padding: 2rem 0;
            font-size: 2rem;
            font-weight: 100;
        }

        .input {
            display: block;
            width: 85%;
            padding: 1rem;
            outline: none;
            margin: 2rem 0;
            background-color: transparent;
            color: $white;
            font-size: 1.5rem;
        }

        &  select {
            padding: 1rem 2rem;
            outline: none;
        }
        &  fieldset {
            width: 90%;
            margin: 2rem 0;
            font-size: 1.4rem;

            &  legend {
                margin-left: 1rem;
                padding: .5rem 1rem;
            }

            &  input {
                margin: 2rem 1rem;
            }
        }

        &  .officer-container{
                position: relative;
                &:hover {
                    &  .listInp {
                        display: block;
                    }
                }

            &  .listInp {
                display: none;
                width: 80%;
                height: 350%;
                overflow: scroll;
                background-color: black;
                box-shadow: 2px 2px 2rem black;
                position: absolute;
                top: 4rem;
            }
            &  .inpList {
                left: 0;
                padding: .5rem 1rem;
                cursor: pointer;
    
                &:hover {
                    background-color: rgba(66, 66, 66, 0.651);
                }
    
                &  p {
                    font-size: 1.2rem;
                    font-weight: 100;
                }
                &  span {
                    font-size: 1.3rem;
                    font-weight: 900;
                    padding: .5rem 0;
                }
            }
        }
        

        &  button {
            padding: 1rem 3rem;
            border-radius: .5rem;
            border: none;
            box-shadow: 1px 1px 5px $white;

            &:hover {
                background-color: $black;
                color: $white;
                box-shadow: 1px 1px 5px $black;
            }
        }
    }
}