.table-container {
    position: relative;
    z-index: 10;




    .calendar {
        margin: 1rem 0;
        min-width: 25rem;
        position: fixed;
        top: 0;
        left: 50%;
        transform: translate(-50%);
        box-shadow: 1px 1px 1rem rgb(199, 199, 199);
        border-radius: 1rem;
        &  .header {
            background-color: $primary-orange;
            color: $white;
            font-size: 1.5rem;
            padding: 1rem;
            // border-radius: .5rem;
            text-align: center;
            cursor: pointer;
        }

        &  input {
            width: 100%;
            padding: 1rem;
        }

        &  .days {
            // border-radius: .5rem;
            display: flex;
            background-color: $white;

            &  button {
                padding: 2rem 1.5rem;
                border: none;
                border-right: 1px solid rgb(245, 229, 201);
                background-color: transparent;
                color: rgb(163, 163, 163);
                transition: all .1s;
                position: relative;

                &:last-child {
                    border: none;
                }

                &::after {
                    content: "";
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: 0;
                    left: 0;
                    background-color: $primary-orange;
                    opacity: 0;
                    transition: all .5s;
                }

                &:hover {
                    color: $black;

                    &::after {
                        opacity: 1;
                        top: 97%;
                        bottom: 0;
                        height: .2rem;
                    }
                }
            }
        }
    }

    &  form {
        margin: 2rem auto;
        font-size: 1.5rem;
        display: flex;
        align-items: center;

        &  label {
            padding: .5rem;
        }
        &  input {
            padding: .5rem;
            display: block;
        }
        &  button {
            padding: .8rem 2rem;
            background-color: black;
            color: white;
            margin-top: 1.5rem;
            border: none;
        }
    }

    .payment-cta {
        position: fixed;
        bottom: 2rem;
        right: 2rem;
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
        background-color: black;
        box-shadow: .5rem .5rem 1rem rgba(0, 0, 0, 0.705);
        z-index: 1000000000000;
        cursor: pointer;

        .next {
            opacity: 0;
            transform: translateX(100%);
            transform-origin: right;
            transition: all .3s ease-out;
            background-color: transparent;
            margin: 0 2rem;
            color: $secondary-blue;
            text-shadow: .2rem .2rem 1rem rgb(58, 58, 58);
            font-size: 2rem;
            border: none;
        }
        .save {
            position: fixed;
            bottom: 2rem;
            right: 5rem;
            z-index: 10000;
            cursor: pointer;
        }
        .pay {
            position: fixed;
            bottom: 9rem;
            right: 5rem;
            z-index: 10000;
            cursor: pointer;
            transform: translate(90%, 100%);
        }
        .ignite {
            opacity: 1;
            transform: rotate(-135deg);
            color: $primary-orange;
        }

        &:hover {
            &  .next {
                opacity: 1;
                transform: none;
            }
            &  .pay {
                opacity: 1;
                transform: translate(90%, 0) rotate(-90deg);
            }
        }
    }

    &  .totalamount {
        font-size: 2rem;
        font-weight: 900;
        text-align: center;
        padding: 2rem;
    }

    &   .table_loader {
        width: 70vw;
        margin: auto;
        text-align: center;
    
        &  .loader {
            animation: loader infinite 2s alternate;
            width: 5rem;
            margin: auto;
            margin-top: 5rem;
        }
    }

    &  .otpmodal {
        position: fixed;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: min-content;
        padding: 3rem;
        text-align: center;
        box-shadow: 1px 1px 1rem rgba(39, 39, 39, 0.712);
        border-radius: .5rem;
        z-index: 10000000;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        background-color: $white;

        &  h3 {
            padding: 1rem 0;
            font-size: 2rem;
        }

        &  .cta {
            margin-top: 2rem;
            &  button {
                padding: 1rem 3rem;
                background-color: $primary-blue;
                color: $white;
                border: none;
                border-radius: .5rem;
            }
        }
    }

    &  .payment-modal {
        position: fixed;
        top: 0;
        left: 0;
        width: 100vw;
        padding: 2rem 0;

        height: 100vh;
        overflow: scroll;
        background-color: rgba(0, 0, 0, 0.514);
        z-index: 10;

        &  .container{
            width: 70vw;
            
            margin: auto;
            background-color: $white;
            padding: 2rem;
            font-size: 1.3rem;
            position: relative;
            overflow: visible;
            padding-top: 5rem;

            &  header {
                text-align: center;
                display: flex;
                justify-content: space-between;
                position: fixed;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
                width: 100%;
                background-color: $white;
                padding: 2rem 0;
                width: 70vw;
                margin: auto;
                margin-top: 2rem;

                &  .selectall {
                    padding: .5rem;
                }
                &  .amount {
                    font-size: 1.7rem;
                    font-weight: 300;
                    color: rgb(51, 51, 51);
                }

                &  .servicech {
                    color: $primary-orange;
                    font-weight: 200;
                    font-size: 1.5rem;
                }
                &  .total {
                    font-size: 1.8rem;
                }
            }

            &  .inv {
                padding: 1rem;
                border: 1px solid rgba(230, 230, 230, 0.548);
                display: grid;
                grid-template-columns: minmax(2rem, 4rem) repeat(5, 1fr) min-content;
                align-items: center;
                text-align: left;

                &  input {
                    width: min-content;
                }

                &  .name {
                    font-weight: 900;
                    font-size: 1.3rem;
                }
                &  .account {
                    display: flex;
                    align-items: center;
                    
                    &  .bankname {
                        font-size: 1.1rem;
                        font-weight: 500;
                    }
                }
                &  p, .amount {
                    padding: .2rem 1rem;
                }

                &  .marker {
                    font-size: 1.3rem;
                }
                &  .correct {
                    color: rgb(28, 202, 28);
                }
                &  .wrong {
                    color: red;
                }
            }
        }

        &  .cta {
            display: flex;
            justify-content: space-between;
            align-items: center;
            margin: 2rem 0;

            &  button {
                padding: 1rem 2rem;
                background-color: transparent;
                border: 1px solid currentColor;
            }
        }
    }

    &  .table-head {
        display: flex;
        align-items: center;
        justify-content: center;

        &   .tabletype {
            text-transform: uppercase;
            padding: 1rem;
            font-size: 2rem;
            text-align: center;
        }
        &  .listlength {
            margin-left: 2rem;
            font-size: 1.2rem;
            background-color: $primary-blue;
            padding: .5rem 1rem;
            border-radius: 10rem;
        }

       
    }

    

    &   .table-cta {
        position: absolute;
        top: 50%;
        left: 0;
        width: 100%;
        display: flex;
        justify-content: space-between;

        &  button {
            padding: .5rem 1rem;
            color: $white;
            background-color: $secondary-blue;
            border: none;
            opacity: .2;

            &:hover {
                opacity: 1;
            }
        }
    }

    &  .table {
        height: 70vh;
        width: 99%;
        margin: auto;
        overflow: scroll;

        &   table {

            &  tr {
                transition: all 1s;
                cursor: pointer;
        
                &  .stage-box {
                    display: flex;
                    align-items: center;
                    justify-content: space-between;
        
                    &  .stars {
                        display: flex;
                        font-size: 1rem;   
                    }
                }

                &  .fast-update {
                    opacity: 0;
                    transform: translateX(10%);
                    z-index: -1;
                    transition: all .5s;

                    &  .push {
                        background-color: $primary-orange;
                        color: $white;
                        border: none;
                        padding: .5rem;
                        box-shadow: 2px 2px 5px black;
                        border-radius: .5rem;
                        transition: all .5s;

                        &:hover {
                            transform: translateX(-5%);
                        }
                    }
                }

                

                &:hover .fast-update {
                    transform: none;
                    opacity: 1;
                    z-index: 100;
                }
            }
        
            &  tr:hover {
                background-color: $lightgray;
            }
            &  th {
                padding: 1rem;
                font-size: 1.5rem;
                text-align: left;
                border: 1px solid $lightbordercolor;
                background-color: $lightgray;
            }
            &  td {
                padding: 1rem;
                font-size: 1.3rem;
                border: 1px solid $lightbordercolor;
                text-align: left;
                font-weight: 100;
        
                &  a {
                    color: black;
                    text-decoration: none;
                    font-weight: 600;
                }
                &  .loader {
                    animation: loader infinite 1s alternate;
                }

                &  .status {
                    background-color: $black;
                    width: 2rem;
                    height: 5rem;
                    padding-top: .1rem;
                    border-radius: 1rem;
                    transform: rotate(90deg);
                    margin: auto;
                    box-shadow: .2rem .2rem 1rem black;

                    &  .light {
                        width: 1rem;
                        height: 1rem;
                        margin: .5rem auto;
                        border-radius: 50%;
                        background-color: $white;
                    }
                    &  .red {
                        background-color: rgb(255, 0, 0);
                        position: relative;
                        opacity: .5;
                    }
                       &  .redblink {
                        background-color: rgb(255, 0, 0);
                        position: relative;
                        &::before {
                            animation: status infinite 1s;
                            content: '';
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            background-color: inherit;
                            width: 100%;
                            height: 100%;
                            border-radius: 50%;
                        }
                        &  .redblink2 {
                            background-color: rgb(4, 0, 255);
                            position: relative;
                            &::before {
                                animation: status infinite .2s;
                                content: '';
                                position: absolute;
                                top: 0;
                                left: 0;
                                right: 0;
                                background-color: inherit;
                                width: 100%;
                                height: 100%;
                                border-radius: 50%;
                            }
                        }
                    }
                    &  .orange {
                        background-color: rgb(255, 211, 14);
                        position: relative;
                        opacity: .5;
                    }
                    &  .orangeblink {
                        background-color: rgb(255, 211, 14);
                        position: relative;
                        &::before {
                            animation: status infinite 1s;
                            content: '';
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            background-color: inherit;
                            width: 100%;
                            height: 100%;
                            border-radius: 50%;
                        }
                    }
                    &  .green {
                        background-color: rgb(91, 255, 91);
                        position: relative;
                        opacity: .5;
                    }
                     &  .greenblink {
                        background-color: rgb(91, 255, 91);
                        position: relative;
                        &::before {
                            animation: status infinite 1s;
                            content: '';
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            background-color: inherit;
                            width: 100%;
                            height: 100%;
                            border-radius: 50%;
                        }
                    }
                    &  .purple {
                        background-color: rgb(214, 52, 255);
                        position: relative;
                        &::before {
                            animation: status infinite 1s;
                            content: '';
                            position: absolute;
                            top: 0;
                            left: 0;
                            right: 0;
                            background-color: inherit;
                            width: 100%;
                            height: 100%;
                            border-radius: 50%;
                        }
                    }
                }

                &  .encashment {
                    text-align: center;
                    
                    &  form {
                        padding: 1rem;
                        background-color: $white;

                        &  input {
                            padding: 1rem;
                            width: 75%;
                            border: 1px solid rgb(201, 201, 201);
                            margin: 1rem auto;
                            display: block;
                        }
                        &  button {
                            padding: .5rem 1rem;
                            background-color: $primary-blue;
                            color: $white;
                            border: none;
                            border-radius: .5rem;
                            text-align: right;
                        }
                    }
                }
            }
        
            &  .vanish {
                @media only screen and (max-width: $bpmedium) {
                    display: none;
                }
            }
            
        }
        
    }

    &  .expand {
        height: 100%;
    }

    &  .updateall {
        padding: 1rem 3rem;
        background-color: $primary-orange;
        border: none;
        margin: 2rem 0;
        color: $white;

        &:hover {
            background-color: $black;
        }
    }
    
}


.globalfilter {
    width: 80%;
    padding: 1rem;
    font-size: 1.5rem;
    margin: auto;
}