.landing_page {
    height: 100vh;
    width: 100vw;
    overflow: hidden;
    background-image: radial-gradient(rgba(0, 0, 0, 0.74)40%,
     rgba(0, 0, 0, 0.788)80%), url(../../img/background.jpeg);
     background-size: cover;

     @media only screen and (max-width: $bpsmallest) {
        background-position: right;
    }
    position: relative;

    .independence {
        position: fixed;
        top: 3rem;
        right: 3rem;
        width: 11rem;
        height: 11rem;
        text-align: center;
        border-radius: 50%;
        padding: 1rem;
        z-index: 1000;
        box-shadow: 1px 1px 1rem white;

        &  img {
            width: 50%;
            margin: auto;
            margin-top: 2.5rem;
            
        }
        &  p {
            font-size: 1.5rem;
            font-weight: 100;
            color: white;
            padding: 1rem 0;
            text-align: center;
        }
    }
    @keyframes fsp {
        from {
            transform: rotate(0deg);
        }to {
            transform: rotate(360deg);
        }
    }
    .fsp {
        
        position: fixed;
        top: 3rem;
        left: 3rem;
        width: 11rem;
        height: 11rem;
        text-align: center;
        border-radius: 50%;
        padding: 1rem;
        z-index: 1000;
        box-shadow: 1px 1px 1rem white;

        &  img {
            animation: fsp 10s infinite ease-in-out;
            width: 50%;
            margin: auto;
            margin-top: 1rem;
            
        }
        &  p {
            font-size: 1.5rem;
            font-weight: 100;
            color: white;
            padding: 1rem 0;
            text-align: center;
        }
    }

    &  .particles {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        height: 100%;
        z-index: 1;
    }

    &  .signin {
        position: relative;
        z-index: 100;
        padding: 2rem 1rem;
        animation: signin infinite 2s alternate;
        background-color: white;
        width: 30rem;
        color: white;
        margin-top: 50vh;
        margin-left: 50vw;
        transform: translate(-50%,-50%);
        border: 1rem solid blue($color: #000000);
        box-shadow: 3px 3px 2rem rgb(172, 172, 172);

        &  header {
            padding: 1rem;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            color: $black;

            &  .logo {
                width: 5rem;
            }

            &  .heading {
                margin-left: 1rem;
                color: $primary-orange;

                &  span {
                    background-image: linear-gradient(to right, $primary-red 65%, $primary-blue 70%);
                    display: block;
                    color: transparent;
                    background-clip: text;
                }
            }

            
        }
        &  .form {
            padding: 0 2rem;
            margin-bottom: 3rem;

            &  div {
                display: flex;
                align-items: center;
                margin: 4rem 0;

                &  .input {
                    padding: 1rem;
                    font-size: 1.5rem;
                    margin-left: 2rem;
                    outline-color: $primary-blue;
                }
            }

            

            &  .cta {
                display: flex;
                align-items: center;
                margin: 0;
                
                &  button {
                    margin-bottom: 2rem;
                }

                &  .loader {
                    animation: loader infinite 1.5s alternate;
                    width: 3rem;
                    margin: 0;
                    padding: 0;
                    align-self: flex-start;
                    margin-right: 2rem;
                }
            }

            &  p {
                color: $lightgray;
                font-size: 1.2rem;
                font-style: italic;

                &  .link {
                    color: $primary-orange;
                    text-align: right;
                    align-self: flex-end;
                }
            }
        }
    }

    &  .error {
        font-size: 1.5rem;
        color: rgb(255, 255, 255);
        font-style: italic;
        font-weight: 100;
        position: absolute;
        bottom: 2rem;
        right: 2rem;
        padding: 1rem 2rem;
        border-radius: 5rem;
        border-bottom-right-radius: 0;
        background-color: rgba(255, 0, 0, 0.288);

        @media only screen and (max-width: $bpsmallest) {
            background-color: rgba(255, 0, 0, 0.651);
            bottom: 1rem;
            right: 1rem;
            border-radius: 0;
        }
    }
}