.marketing-board {
    background-color: $light-background;
    height: 100vh;

    &  .header {
        background-color: $light-primary-orange;
        padding: 1rem;
        display: flex;
        justify-content: space-between;
        align-items: center;

        &  .logo {
            width: 5rem;
            margin-right: 1rem;
        }

        &  .heading {
            color: $black;
        }

        &  .back {
            justify-self: flex-end;
            text-align: right;
            background-color: transparent;
            padding: .5rem 1.5rem .5rem .5rem;
            color: $bordercolor;
            border: none;
            border-bottom: 1px solid currentColor;
        }
    }
    &  h2 {
        font-size: 1.7rem;
        padding: 1rem;
        border-bottom: 1px solid currentColor;
    }

    &  .body {
        display: grid;
        grid-template-columns: repeat(8, 1fr);
        padding: 2rem;

        &  .main {
            box-shadow: 1px 1px 5px rgb(155, 155, 155);
            height: 80vh;
            overflow: scroll;

            @media only screen and (max-width: $bplarge) {
                position: absolute;
                top: 30vh;
                left: 0;
                width: 100vw;
                background-color: $light-background;
                z-index: 1000;
              }

            .pic {
                position: relative;
                overflow: hidden;
                border-radius: 50%;
                margin: 1rem;
                width: 15rem;
                height: 15rem;
                margin-bottom: -20%;


                &  img {
                    z-index: 1000;
                    width: 15rem;
                    height: 15rem;
                    border-radius: 50%;
                }
            
                &  label {
                    opacity: 0;
                    position: absolute;
                    bottom: 0;
                    left: 50%;
                    transform: translate(-50%, 0);
                    background-color: rgba(0, 0, 0, 0.699);
                    width: 90%;
                    text-align: center;
                    height: 10%;
                    padding: 1rem;
                    cursor: pointer;
                    transition: all .5s;
            
                    &  .font {
                        color: $white;
                    }
            
                    &  input {
                        display: none;
                    }
                }
            
                &:hover label {
                    opacity: 1;
                }
            
                &  .loader {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%,-50%) scale(.2);
                }
            }
            // &  .img {
            //     background-color: $secondary-blue;
            //     padding: 2rem;
            //     position: relative;

                
            //     &  img {
            //         z-index: 1000;
            //         width: 15rem;
            //         border: .5rem solid $secondary-blue;
            //         border-radius: 50%;
            //         margin-bottom: -10%;
            //     }
            // }
            &  .details {
                padding: 1rem;
                margin-left: 18rem;
                border-top: 1px solid $lightgray;
                
                &  .name {
                    font-size: 1.7rem;
                    font-weight: 700;
                    padding: .5rem 0;
                }
                &  .role {
                    font-size: 1.2rem;
                    font-weight: 100;
                }

                &  ul {

                    &  li {
                        font-size: 1.5rem;
                        padding: 1rem 0;
                        list-style: none;
                        font-weight: 900;
                        display: flex;
                        justify-content: space-between;
                        border-bottom: 1px solid rgb(224, 224, 224);
                        padding: 1rem 2rem;

                        &  span {
                            font-weight: 100;
                            margin-right: 2rem;
                        }
                    }
                    
                }
            }

            &  .progress-bar {
                width: 90%;
                margin: 2rem auto;
                margin-top: 5rem;
                background-color: $secondary-blue;
                border-radius: 1rem;
                height: 10%;
                padding: .5rem;

                &  .block {
                    width: 57%;
                    height: 100%;
                    background-color: $lightgray;
                    border-top-left-radius: .5rem;
                    border-bottom-left-radius: .5rem;
                    position: relative;
                    transition: all 3s;

                    &  .percentage {
                        position: absolute;
                        top: 0;
                        right: 0;
                        font-size: 1.7rem;
                        padding: 1rem;
                        display: flex;
                        align-items: center;

                        &  span {
                            font-size: 3rem;
                            margin-right: 1rem;
                        }
                    }
                }
            }

            &  .new-target {
                text-align: center;
                font-size: 5rem;
                color: $secondary-blue;
                font-weight: 900;
                margin: 5rem 0;
            }

            &  form {
                padding: 2rem;
                &  input, select {
                    display: block;
                    padding: 1rem;
                    outline: none;
                    margin: 1rem 0;
                }
            }

            &  .converts {
                width: 95%;
                margin: 3rem auto;
                height: 30vh;
                overflow: scroll;

                &  .head {
                    display: flex;
                    padding: 1rem 2rem;
                    justify-content: space-between;
                    align-items: center;
                    background-color: rgb(219, 219, 219);
                    &  .heading {
                        padding: 0;
                        border: none;
                    }
                }

                &  .investor {
                    display: grid;
                    grid-template-columns: repeat(4, 1fr);
                    font-size: 1.2rem;
                    border-collapse: collapse;

                    &  p{
                        border: 1px solid rgb(219, 219, 219);
                        padding: 1rem;
                    }
                }
            }
            &  .monthly {
                width: 95%;
                margin: 3rem auto;
                height: 30vh;
                overflow: scroll;
            }
        }
        &  .leaderboard {
            position: relative;
            z-index: 10;
            grid-column: 7/9;
            height: 80vh;
            overflow: scroll;
            box-shadow: 1px 1px 5px rgb(77, 77, 77);
            border-radius: .5rem;

            @media only screen and (max-width: $bplarge) {
                grid-column: 1/-1;
              }

            &  input {
                padding: 1rem 2rem;
                width: 80%;
                margin: 1rem;
                background-color: transparent;
                outline: none;
                border-radius: 10rem;
                border: 1px solid $lightgray;
                position: relative;
            }

            &  .lilength {
                position: absolute;
                top: 0;
                right: 0;
                font-size: 1.6rem;
                background-color: $primary-blue;
                padding: .5rem 1rem;
                box-shadow: 1px 1px 5px black;
            }

            &  .marketer {
                padding: 1rem 2rem;
                display: flex;
                align-items: center;
                cursor: pointer;
                transition: all .5s;

                &:hover {
                    background-color: rgb(226, 226, 226);

                    &  .img img {
                        transform: rotate(-20deg);
                    }
                }
                &  .img {
                    border: 1px solid $secondary-blue;
                    border-radius: 50%;

                    &  img {
                        width: 5rem;
                        height: 5rem;
                        border-radius: 50%;
                        transition: all .5s;
                    }
                }
                &  p {
                    font-size: 1.7rem;
                    margin-left: 2rem;
                }
            }
        }
    }
}